* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header {
  height: 64px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1), 0 2px 2px rgba(0, 0, 0, 0.15);
}

header div {
  padding: 16px;
  max-width: 980px;
  margin: 0 auto;
}

footer {
  max-width: 720px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90vw;
  padding-bottom: 32px;
}

.card {
  width: 320px;
  margin: 8px 16px;
  padding: 16px;
  background: #f6f6f6;
  border-radius: 4px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1), 0 8px 8px rgba(0, 0, 0, 0.15);
}

.container {
  max-width: 980px;
  margin: 48px auto;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
}

.card-info {
  display: flex;
  justify-content: space-between;
  padding-top: 16px;
}

.card-title {
  font-weight: 500;
}

.confirmed {
  display: flex;
  align-items: baseline;
  padding-left: 16px;
}

.confirmed h5 {
  font-weight: 400;
  color: #757272;
  font-size: 14px;
}

.confirmed h3 {
  padding-left: 16px;
  color: #6c757d;

  font-size: 32px;
}

.stats-container {
  max-width: 720px;
  width: 90vw;
  margin: 16px auto;
}

.stats-title {
  text-align: left;
  font-size: 24px;
  padding: 8px 16px 0 16px;
  font-weight: 400;
}
